@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Mono";
  font-display: swap;
  src: url("../fonts/pt-mono-regular.woff2") format("woff2");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/roboto-condensed-regular.woff2") format("woff2");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/roboto-condensed-bold.woff2") format("woff2");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  font-family: "PT Mono", "Arial", sans-serif;
  color: #041827;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #041827;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #041827;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 30px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 50px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 25px;
    min-width: 320px;
  }
}

@media (max-width: 767px) {
  .accordion__element:last-child {
    margin-bottom: 0;
  }
  .accordion__element.is-active > .accordion__button {
    opacity: 0.5;
  }
  .accordion__button {
    position: relative;
  }
  .accordion__button-text::before {
    content: "";
    display: block;
    position: absolute;
    top: 26px;
    right: 2px;
    width: 15px;
    height: 1px;
    background-color: #f8fdff;
  }
  .accordion__button-text::after {
    content: "";
    display: block;
    position: absolute;
    top: 26px;
    right: 2px;
    width: 15px;
    height: 1.5px;
    transform: rotate(90deg);
    background-color: #f8fdff;
  }
  .accordion__element.is-active .accordion__button-text::before {
    content: "";
    display: block;
    position: absolute;
    top: 26px;
    right: 2px;
    width: 15px;
    height: 1px;
    background-color: #f8fdff;
  }
  .accordion__element.is-active .accordion__button-text::after {
    display: none;
  }
}
.advantages {
  padding-top: 120px;
}
@media (max-width: 1023px) {
  .advantages {
    padding-top: 80px;
  }
}
@media (max-width: 767px) {
  .advantages {
    padding-top: 48px;
  }
}

.advantages__list {
  margin: 0;
  padding: 0;
  margin-bottom: 120px;
  list-style-type: none;
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 31px;
  row-gap: 29px;
}
@media (max-width: 1023px) {
  .advantages__list {
    margin-bottom: 74px;
    column-gap: 29px;
    row-gap: 30px;
  }
}
@media (max-width: 767px) {
  .advantages__list {
    margin-bottom: 50px;
    grid-template-columns: 1fr;
  }
}

.advantages__list-item {
  padding: 40px 45px 68px 40px;
  border: 1px solid #ebebeb;
  border-left: 5px solid #00b2ff;
  min-height: 300px;
}
.advantages__list-item:hover {
  background-color: #00b2ff;
  color: #f2f5f7;
}
.advantages__list-item:hover .advantages__icon, .advantages__list-item:hover .advantages__title {
  color: #f2f5f7;
}
@media (max-width: 1023px) {
  .advantages__list-item {
    padding: 40px 42px 30px 41px;
    min-height: 360px;
  }
}
@media (max-width: 767px) {
  .advantages__list-item {
    padding: 30px 30px 45px 25px;
    min-height: 325px;
  }
}

@media (max-width: 767px) {
  .advantages__list-item:nth-child(-n+2) {
    min-height: 340px;
  }
}

.advantages__icon {
  color: #00b2ff;
  margin-bottom: 15px;
}
@media (max-width: 1023px) {
  .advantages__icon {
    margin-bottom: 10px;
  }
  .advantages__icon--privacy {
    margin-bottom: 8px;
  }
  .advantages__icon--cost {
    margin-bottom: 5px;
  }
}
@media (max-width: 767px) {
  .advantages__icon {
    width: 30px;
    height: 30px;
  }
  .advantages__icon--privacy {
    margin-bottom: 13px;
  }
  .advantages__icon--cost {
    margin-bottom: 13px;
  }
}

.advantages__title {
  margin: 0;
  margin-bottom: 12px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 34px;
  color: #00b2ff;
}
@media (max-width: 1023px) {
  .advantages__title {
    margin-bottom: 6px;
  }
  .advantages__title--cost {
    margin-bottom: 2px;
  }
}
@media (max-width: 767px) {
  .advantages__title {
    font-size: 20px;
    line-height: 28px;
    margin-bottom: 10px;
  }
  .advantages__title--privacy {
    margin-bottom: 13px;
  }
  .advantages__title--cost {
    margin-bottom: 13px;
  }
}

.advantages__text {
  margin: 0;
}
@media (max-width: 767px) {
  .advantages__text {
    font-size: 14px;
    line-height: 19px;
  }
}

.btn {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  align-items: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  display: inline-flex;
  padding: 10px 15px;
  color: #f2f5f7;
  background-color: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
  border-radius: 3px;
  transition: opacity 0.3s ease;
}
.btn--submit {
  background: #041427;
  font-size: 16px;
  line-height: 19px;
  color: #ffffff;
  padding: 15px 25px 13px 25px;
  display: flex;
  align-items: center;
  justify-content: center;
  order: -1;
}
@media (max-width: 1023px) {
  .btn--submit {
    padding: 16px 25px 12px 25px;
  }
}
.btn--info {
  color: #00b2ff;
  border: 1px solid #00b2ff;
  padding: 14px 13px;
  margin-top: 20px;
  margin-bottom: 20px;
  min-width: 180px;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (max-width: 1023px) {
  .btn--info {
    margin-top: 0;
    min-width: auto;
    width: 36.4%;
  }
}
@media (max-width: 767px) {
  .btn--info {
    margin-top: 0;
    padding: 8px 15px;
    width: 59.3%;
  }
}
.btn--header {
  border: 1px solid #f2f5f7;
  margin-top: 5px;
  margin-bottom: 10px;
  margin-right: 62px;
  max-width: 250px;
}
@media (max-width: 1023px) {
  .btn--header {
    display: none;
    margin-right: 0;
  }
}
.btn--offer {
  background: #00b2ff;
  font-size: 18px;
  line-height: 21px;
  padding: 15px 27px;
}
@media (max-width: 1023px) {
  .btn--offer {
    margin-left: 45px;
    padding: 14px 27px 16px 27px;
  }
}
@media (max-width: 767px) {
  .btn--offer {
    font-size: 14px;
    line-height: 16px;
    letter-spacing: 0.05em;
    padding: 12px 30px;
    width: 100%;
    margin-left: 0;
  }
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
.btn:hover, .btn:focus {
  opacity: 0.5;
}
.btn:active {
  opacity: 0.3;
}

.catalog {
  padding-top: 65px;
  padding-bottom: 140px;
  background-color: #041827;
}
@media (max-width: 1023px) {
  .catalog {
    padding-top: 55px;
    padding-bottom: 75px;
  }
}
@media (max-width: 767px) {
  .catalog {
    padding-top: 45px;
    padding-bottom: 86px;
  }
}

.catalog__title {
  margin: 0;
  margin-bottom: 52px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 51px;
  color: #ffffff;
}
@media (max-width: 1023px) {
  .catalog__title {
    margin-bottom: 35px;
  }
}
@media (max-width: 767px) {
  .catalog__title {
    font-size: 28px;
    line-height: 39px;
    margin-bottom: 30px;
  }
}

@media (max-width: 767px) {
  .catalog__title-text--desktop {
    display: none;
  }
}
.catalog__title-text--mobile {
  display: none;
}
@media (max-width: 767px) {
  .catalog__title-text--mobile {
    display: inline;
  }
}

.catalog__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 85px;
}
@media (max-width: 1023px) {
  .catalog__list {
    grid-template-columns: 1fr;
    gap: 30px;
  }
}

.card {
  position: relative;
  min-height: 650px;
  background-color: #041427;
}
.card:hover, .card:focus {
  opacity: 0.6;
}
.card:active {
  opacity: 0.4;
}
@media (max-width: 1023px) {
  .card {
    min-height: 250px;
  }
}
@media (max-width: 767px) {
  .card {
    min-height: auto;
    max-height: 250px;
  }
}

.card__link {
  display: block;
  width: 100%;
  height: 100%;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 30px;
  color: #ffffff;
}
.card__link:hover, .card__link:focus {
  opacity: 0.5;
}
.card__link:active {
  opacity: 0.4;
}
.card__link::before {
  content: "";
  display: block;
  position: absolute;
  width: 60px;
  height: 60px;
  bottom: 0;
  right: 0;
  background-color: #00b2ff;
  background-image: url("../img/svg/arrow-right.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 4px 8px;
}
@media (max-width: 767px) {
  .card__link::before {
    width: 40px;
    height: 40px;
    background-size: 3px 6px;
  }
}
@media (max-width: 767px) {
  .card__link {
    font-size: 18px;
    line-height: 30px;
  }
}

.card__img {
  display: block;
  width: 350px;
  height: 650px;
  object-fit: cover;
}
@media (max-width: 1023px) {
  .card__img {
    width: 923px;
    height: auto;
  }
}
@media (max-width: 767px) {
  .card__img {
    width: 717px;
    height: auto;
    max-height: 250px;
  }
}

.card__text {
  position: absolute;
  top: 50px;
  left: 40px;
  max-width: 77.1%;
}
@media (max-width: 1023px) {
  .card__text {
    top: 40px;
    left: 45px;
    max-width: 86.5%;
  }
}
@media (max-width: 767px) {
  .card__text {
    top: 25px;
    left: 30px;
    max-width: 77.7%;
  }
}

.card__text-part--br br {
  display: none;
}
@media (max-width: 1023px) {
  .card__text-part--br br {
    display: inline;
  }
}
@media (max-width: 1023px) {
  .card__text-part--br-mobile br {
    display: none;
  }
}

.custom-input,
.custom-textarea {
  position: relative;
}
.custom-input.is-invalid input,
.custom-input.is-invalid textarea,
.custom-textarea.is-invalid input,
.custom-textarea.is-invalid textarea {
  border: 2px solid #ff1553;
}
.custom-input.is-invalid input:focus, .custom-input.is-invalid input:hover,
.custom-input.is-invalid textarea:focus,
.custom-input.is-invalid textarea:hover,
.custom-textarea.is-invalid input:focus,
.custom-textarea.is-invalid input:hover,
.custom-textarea.is-invalid textarea:focus,
.custom-textarea.is-invalid textarea:hover {
  border: 2px solid rgba(255, 21, 83, 0.5);
}
.custom-input.is-valid input,
.custom-textarea.is-valid input {
  border: 2px solid #22b259;
}
.custom-input.is-valid input:focus, .custom-input.is-valid input:hover,
.custom-textarea.is-valid input:focus,
.custom-textarea.is-valid input:hover {
  border: 2px solid rgba(34, 178, 89, 0.5);
}
.custom-input .input-message,
.custom-textarea .input-message {
  position: absolute;
  left: 15px;
  bottom: -20px;
  font-size: 14px;
}
.custom-input .input-message.is-invalid,
.custom-textarea .input-message.is-invalid {
  color: #ff0000;
}
.custom-input .input-message.is-valid,
.custom-textarea .input-message.is-valid {
  color: #15ab07;
}

.custom-toggle {
  position: relative;
}
.custom-toggle.is-invalid label span {
  color: #ff0000;
}
.custom-toggle .input-message {
  position: absolute;
  right: 0;
  bottom: -55px;
  font-size: 14px;
}
.custom-toggle .input-message.is-invalid {
  color: #ff0000;
}

.custom-toggle--modal .input-message {
  position: absolute;
  right: 0;
  bottom: -20px;
