  padding: 0;
  background: rgba(255, 255, 255, 0);
  color: #ffffff;
  border: none;
  cursor: pointer;
}
.modal__close-btn:hover, .modal__close-btn:focus {
  color: rgba(4, 24, 39, 0.5);
}
.modal__close-btn:active {
  color: #041827;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

