@include vp-767 {
  .accordion {
    &__element:last-child {
      margin-bottom: 0;
    }

    &__element.is-active > &__button {
      opacity: 0.5;
    }

    &__button {
      position: relative;
    }
  }

  .accordion__button-text {
    &::before {
      @include pseudo ();
      top: 26px;
      right: 2px;
      width: 15px;
      height: 1px;
      background-color: $color-social-icon;
    }

    &::after {
      @include pseudo ();
      top: 26px;
      right: 2px;
      width: 15px;
      height: 1.5px;
      transform: rotate(90deg);
      background-color: $color-social-icon;
    }
  }

  .accordion__element.is-active .accordion__button-text {
    &::before {
      @include pseudo ();
      top: 26px;
      right: 2px;
      width: 15px;
      height: 1px;
      background-color: $color-social-icon;
    }

    &::after {
      display: none;
    }
  }
}
